import { useQuery } from '@tanstack/react-query';
import { AxiosResponse } from 'axios';
import { Response, getAllBrandsService } from '../services/brands';

const useBrands = () => {
  const { data = [], isLoading } = useQuery<AxiosResponse<Response>, unknown>({
    queryKey: ['todos'],
    queryFn: getAllBrandsService,
  });

  return {
    isLoading,
    data: data as any[],
  };
};

export default useBrands;
