import { useTranslation } from 'react-i18next';
import Carousel from 'react-multi-carousel';
import { mediaScreenMobile, mediaScreenTablet } from 'utils/constants';
import { carouselsData } from './const';

const responsive = {
  desktop: {
    breakpoint: { max: 3000, min: mediaScreenTablet },
    items: 1.3,
  },
  tablet: {
    breakpoint: { max: mediaScreenTablet, min: mediaScreenMobile },
    items: 2.07,
  },
  mobile: {
    breakpoint: { max: mediaScreenMobile, min: 325 },
    items: 1,
  },
};

function Carousel3() {
  const { t } = useTranslation();

  return (
    <div className="container carousel-brands">
      <h3 className="carousel-brands__title">
        {t('Khám phá ưu đãi từ các thương hiệu')}
      </h3>
      <div className="carousel3">
        <Carousel
          arrows={false}
          responsive={responsive}
          itemClass="carousel3__item"
        >
          {carouselsData.map((c, i) => (
            <div key={i} className="carousel3__wrap">
              <div className="carousel3__img">
                <img className="cover-img" src={c.url} alt="brands" />
              </div>
            </div>
          ))}
        </Carousel>
      </div>
    </div>
  );
}

export default Carousel3;
