import { Modal } from 'antd';
import cx from 'classnames';
import FundiinButton from 'components/Common/FundiinButton';
import { useWindowSize } from 'hooks';
import { cloneDeep } from 'lodash';
import useBrands from 'pages/Brands/hooks/useBrands';
import React, { useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { isSmallerPc } from 'utils/helper';
import MenuMobile from './MenuMobile';
import { Brands, Buys, EStatue } from './const';

const IMG_URL = process.env.REACT_APP_IMG_URL;

function Category() {
  const { t } = useTranslation();
  const [width] = useWindowSize();
  const { data, isLoading } = useBrands();
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [activeTabs, setActiveTabs] = useState<boolean[]>([true]);
  const [activeTab2, setActiveTab2] = useState(EStatue[EStatue.ONLINE]);

  const result = useMemo(() => {
    const activeBrands = Brands.filter((_, i) => activeTabs[i]).map(
      b => b.altName
    );
    return data.filter(d => activeBrands.includes(d.altName));
  }, [data, activeTabs]);

  const count = useMemo(() => {
    let c = 0;
    result.forEach(r => {
      c += r.merchants.length;
    });
    return c;
  }, [result]);

  const countFilter = useMemo(
    () => activeTabs.filter(ac => !!ac).length,
    [activeTabs]
  );

  const handleActiveTab = (id: number) => {
    const atTabs = cloneDeep(activeTabs);
    atTabs[id] = !atTabs[id];
    setActiveTabs(atTabs);
  };

  const handleActiveTab2 = (id: string) => {
    setActiveTab2(id);
  };

  const handleOpenModel = () => {
    setIsModalOpen(true);
  };

  const handleOk = (vals: boolean[]) => {
    setActiveTabs(vals);
    setIsModalOpen(false);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
  };

  return (
    <div className="container">
      <div className="category">
        <div className="category__sidebar">
          <h3 className="category__title">{t('Danh mục')}</h3>
          <div className="category__menu">
            {Brands.map((b, i) => (
              <FundiinButton
                key={i}
                className={cx([
                  activeTabs[i] ? 'active' : '',
                  isSmallerPc(width) ? 'medium' : '',
                ])}
                onClick={() => handleActiveTab(i)}
              >
                {b.icon} {t(b.text)}
              </FundiinButton>
            ))}
          </div>
          <h3 className="category__title">{t('Mua hàng')}</h3>
          <div className="category__menu">
            {Buys.map((b, i) => (
              <FundiinButton
                key={i}
                className={cx([
                  activeTab2 === b.value ? 'active' : '',
                  isSmallerPc(width) ? 'medium' : '',
                ])}
                onClick={() => handleActiveTab2(b.value)}
              >
                {t(b.text)}
              </FundiinButton>
            ))}
          </div>
        </div>
        <div className="category__result">
          <p className="category__count">
            {count} {t('Kết quả')}
          </p>
          <div className="category__sidebar--mobile">
            {Buys.map((b, i) => (
              <FundiinButton
                key={i}
                className={cx(['mini', activeTab2 === b.value ? 'active' : ''])}
                onClick={() => handleActiveTab2(b.value)}
              >
                {t(b.text)}
              </FundiinButton>
            ))}
            <FundiinButton
              className={cx(['mini filter-btn', countFilter ? 'active' : ''])}
              onClick={handleOpenModel}
            >
              Danh mục {countFilter || ''}
            </FundiinButton>
          </div>
          <div className="category__list">
            {isLoading ? (
              <p>Loading...</p>
            ) : (
              result.map((d, i) => (
                <React.Fragment key={i}>
                  {d.merchants
                    .filter((m: any) => m.runningForm === activeTab2)
                    .map((m: any, ii: number) => (
                      <div key={`${i}-${ii}`} className="category__item">
                        <img
                          className="cover-img"
                          src={IMG_URL + m.image}
                          alt="item"
                        />
                        {m.runningForm ? (
                          <div className="tag">
                            <span>{m.runningForm}</span>
                          </div>
                        ) : null}
                      </div>
                    ))}
                </React.Fragment>
              ))
            )}
          </div>
        </div>
      </div>

      <Modal
        open={isModalOpen}
        title={t('category.Danh mục')}
        footer={<div className="ant-modal-footer__wrap"></div>}
        onCancel={handleCancel}
      >
        <MenuMobile activeTabs={activeTabs} onChange={handleOk} />
      </Modal>
    </div>
  );
}

export default Category;
