import { Checkbox } from 'antd';
import cx from 'classnames';
import PrimaryButton from 'components/Common/PrimaryButton';
import { cloneDeep } from 'lodash';
import { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Brands } from './const';

type PropsMenuMobile = {
  activeTabs: boolean[];
  onChange: (activeTabs: boolean[]) => void;
};

function MenuMobile({ activeTabs, onChange = () => {} }: PropsMenuMobile) {
  const { t } = useTranslation();
  const [intenalActiveTabs, setActiveTabs] = useState<boolean[]>([]);

  const handleActiveTab = (id: number) => {
    const atTabs = cloneDeep(intenalActiveTabs);
    atTabs[id] = !atTabs[id];
    setActiveTabs(atTabs);
  };

  const handleChange = () => {
    onChange(intenalActiveTabs);
  };

  useEffect(() => {
    setActiveTabs(activeTabs);
  }, [activeTabs]);

  return (
    <div className="category__menu--mobile">
      <ul>
        {Brands.map((b, i) => (
          <li key={i} className={cx([intenalActiveTabs[i] ? 'active' : ''])}>
            <span>
              {b.icon} {t(b.text)}
            </span>
            <Checkbox
              checked={!!intenalActiveTabs[i]}
              onChange={() => handleActiveTab(i)}
            ></Checkbox>
          </li>
        ))}
      </ul>
      <PrimaryButton
        type="button"
        className="register-btn"
        onClick={handleChange}
      >
        {t('Áp dụng')}
      </PrimaryButton>
    </div>
  );
}

export default MenuMobile;
